.socials {
    font-family: $font;

    position: relative;
    display: flex;
    flex-flow: row nowrap;
    border-top: 1px solid rgba(255, 255, 255, 0.3);

    &::after {
        content: '';
        position: absolute;
        left: 15px;
        width: calc(100% - 30px);
        height: 25px;
        background: url(https://skill-branch.ru/_nuxt/img/b461c1c.svg) 50% 50% repeat-x;
        background-size: 100%;
        width: 100%;
        left: 0;
        bottom: -75px;
    }

    &__title {
        margin: 0;
        flex: 1 1 45%;
        padding: 22px 25px 22px 0;

        font-size: 24px;
        line-height: 1.2;
        font-weight: 700;
        color: #fff;
    }

    &__list {
        padding: 0;
        margin: 0;
        list-style: none;
        flex: 1 1 55%;
        display: flex;
        flex-flow: row nowrap;
    }

    &__item {
        flex: 1 1 auto;
        border-left: 1px solid rgba(255, 255, 255, 0.3);
    }

    &__link {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        color: #fff;

        transition: color 300ms ease;

        svg {
            width: 24px;
            height: 24px;
            fill: #fff;

            transition: fill 300ms ease;
        }

        &:hover,
        &:active,
        &:focus {
            color: $secondary-color;

            svg {
                fill: $secondary-color;
            }
        }
    }
}

@media (max-width: 992px) {
    .socials {
        display: block;
        border-top: none;

        &::after {
            bottom: -60px;
        }

        &__title {
            padding: 10px 0;
            margin-bottom: 10px;

            font-size: 22px;
        }

        &__list {
            border-top: 1px solid rgba(255, 255, 255, 0.3);
        }

        &__item {
            padding: 10px 0;

            &:first-child {
                border-left: none;
            }
        }
    }
}

@media (max-width: 768px) {
    .socials {
        &::after {
            bottom: -40px;
        }
        &__title {
            text-align: center;
        }
    }
}

@media (max-width: 480px) {
    .socials {
        &__title {
            margin-bottom: 0;

            font-size: 16px;
        }

        &__item {
            padding: 10px 5px;
            width: 10%;
        }
    }
}
